.sobre-mi {
    background-color: #f6e5b3;
    padding: 4rem 1.5rem;
    color: #b84a3c;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }

  #sobre-mi-title-container {
    position: relative;
    width: 100%;
    /* margin-top: 50px; */
    height: 400px; /* o el alto que quieras */
    overflow: hidden;
  }

  #sobre-mi-title-container img {
    width: 100%;
    height: auto;
    object-fit: cover;
    filter: brightness(50%); /* oscurece la imagen */
    transform: translateY(-47%);
  }

  #sobre-mi-title{
    font-family: cursive;
    position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #F6E5B3; /* uno de los colores que us√°s */
  font-size: 3rem;
  z-index: 1;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); /* sombra para resaltar el texto */
  }

  .sobre-mi-section{
    display: flex;
  }

  .sobre-mi-container {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    flex-direction:column;
    gap: 2.5rem;
  }
  
  /* @media (min-width: 768px) {
    .sobre-mi-container {
      flex-direction: row;
      align-items: center;
    }
  } */
  
  .sobre-mi-img {
    flex: 1;
    text-align: center;
  }
  
  .artista-img {
    width: 100%;
    max-width: 400px;
    border: 4px solid #dabec3;
    border-radius: 20px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
  }
  
  .sobre-mi-texto {
    flex: 1;
  }
  
  .sobre-mi-texto h2 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
    color: #b84a3c;
  }
  
  .sobre-mi-texto p {
    font-size: 1.1rem;
    line-height: 1.6;
    color: #4c3b3b;
    margin-bottom: 1rem;
  }
  
  .sobre-mi-texto .negrita {
    font-weight: bold;
  }
  
  .sobre-mi-texto button {
    padding: 0.6rem 1.2rem;
    background-color: #b9c4b1;
    color: #4c3b3b;
    border: none;
    border-radius: 8px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .sobre-mi-texto button:hover {
    background-color: #dabec3;
  }
  